import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import { handleLogin, isLoggedIn } from "../components/auth"
import TempFirebaseComponent from "../components/TempFirebaseComponent"


import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import FormControl from 'react-bootstrap/FormControl'
import Button from 'react-bootstrap/Button'

class Login extends React.Component {
  state = {
    login: ``,
    password: ``,
  }

  handleUpdate(event) {
    this.setState({
      [event.target.name]: event.target.value,
    })
  }

  handleSubmit(event) {
    event.preventDefault();
    handleLogin(this.state);
  }

  render() {
    if (isLoggedIn()) return <Layout>
      <SEO title="Login" />
      <p>Welcome</p>
    </Layout>  //{navigate(`/app/profile`)

    return (
      <Layout>
        <SEO title="Login" />

        <Form className="m-5">
          <Form.Group as={Row} controlId="login">
            <Form.Label column sm="2">Login</Form.Label>
            <Col sm="10">
              <FormControl
                onChange={e => this.handleUpdate(e)}
                placeholder="Login"
                aria-label="login"
                aria-describedby="login"
                name="login"
              />
            </Col>
          </Form.Group>

          <Form.Group as={Row} controlId="formPlaintextPassword">
            <Form.Label column sm="2">Password</Form.Label>
            <Col sm="10">
              <Form.Control
                onChange={e => this.handleUpdate(e)}
                type="password"
                placeholder="Password"
                name="password" />
            </Col>
          </Form.Group>
        </Form>

        {/* <p>{this.state.login}</p>
    <p>{this.state.password}</p> */}

        <Button
          onClick={(e) => this.handleSubmit(e)}
          variant="outline-secondary" type="submit" className="m-5">Log in</Button>

        <TempFirebaseComponent />
        
      </Layout>
    )
  }
}

export default Login


